.TodoListItem {
  padding: 1rem;
  display: flex;
  align-items: center; // vertical center
  &:nth-child(even) {
    background: #f8f9fa;
  }
  .checkbox {
    cursor: pointer;
    flex: 1; // replace all field
    display: flex;
    align-items: center; // vertical center
    svg {
      // icon
      font-size: 1.5rem;
    }
    .text {
      margin-left: 0.5rem;
      flex: 1; 
    }
    // style after checked
    &.checked {
      svg {
        color: #22b8cf;
      }
      .text {
        color: #adb5bd;
        text-decoration: line-through;
      }
    }
  }
  .remove {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color: #ff6b6b;
    cursor: pointer;
    &:hover {
      color: #ff8787;
    }
  }

  // border between elements
  & + & {
    border-top: 1px solid #dee2e6;
  }
}
